<template>
	<div class="content-wrapper">
		<div class="loading-container" v-if="isLoading">
			<!-- <me-loading/> -->
			<div class="loading-wrapper">
				<me-loading />
			</div>
		</div>
		<me-scroll ref="scroll">
			<div class="content">
				<!-- banner -->
				<div class="pic" v-if="content.banner">
					<a :href="content.banner.linkUrl" class="pic-link">
						<img
							@load="updateScroll"
							:src="content.banner.picUrl"
							alt=""
							class="pic-img"
						/>
					</a>
				</div>
				<div
					class="section"
					v-for="(section, index) in content.data"
					:key="index"
				>
					<h4 class="section-title">{{ section.name }}</h4>
					<ul class="section-list">
						<li
							class="section-item"
							v-for="(item, i) in section.itemList"
							:key="i"
						>
							<a :href="item.linkUrl" class="section-link">
								<p class="section-pic" v-if="item.picUrl">
									<img v-lazy="item.picUrl" alt="" class="section-img" />
								</p>
								<p class="section-name">{{ item.name }}</p>
							</a>
						</li>
					</ul>
				</div>
			</div>
		</me-scroll>
		<div class="g-backtop-container">
			<me-backtop @backtop="backToTop" :visible="isBacktopVisible" />
		</div>
	</div>
</template>

<script>
	// 接口: www.imooc.com/api/category/content/id(这里是id) 具体在category.js
	import MeLoading from 'base/loading';
	import MeScroll from 'base/scroll';
	import MeBacktop from 'base/backtop';
	import { getCategoryContent } from 'api/category';
	// 缓存
	import storage from 'assets/js/storage';
	import {
		CATEGORY_CONTENT_KEY,
		CATEGORY_CONTENT_UPDATE_TIME_INTERVAL
	} from './config';

	export default {
		name: 'CategoryContent',
		components: {
			MeLoading,
			MeScroll,
			MeBacktop
		},
		props: {
			curId: {
				type: String,
				default: ''
			}
		},
		data() {
			return {
				content: {},
				isBacktopVisible: false,
				isLoading: true
			};
		},
		watch: {
			curId(id) {
				this.isLoading = true;
				this.getContent(id).then(() => {
					this.isLoading = false;
					this.backToTop(0);
				});
				// console.log(this.getContent);
			}
    },
    
		methods: {
      
			// 获取content数据,然后填充html结构
			// 缓存功能在getContent写
			getContent(id) {
				// 获取本地缓存内容
				let contents = storage.get(CATEGORY_CONTENT_KEY);
				// 保存上次更新时间
				let updateTime;
				// 定义当前时间,获取时间戳
				const curTime = new Date().getTime();

				if (contents && contents[id]) {
					updateTime = contents[id].updateTime || 0;
					if (curTime - updateTime <= CATEGORY_CONTENT_UPDATE_TIME_INTERVAL) {
						// 如果条件成立,就走缓存
						// return 要把它变成promise对象
						return this.getContentByLocalStorage(contents[id]);
					} else {
						// 走http
						// 成功获取之后 then更新内容
						return this.getContentByHTTP(id).then(() => {
							this.updateLocalStorage(contents, id, curTime);
						});
					}
				} else { // 如果获取不到 完全走http
					// 成功获取之后 then更新内容
					return this.getContentByHTTP(id).then(() => {
						this.updateLocalStorage(contents, id, curTime);
					});
				}
			},

			// 获取本地缓存内容
			getContentByLocalStorage(content) {
				this.content = content.data;
				// resolve 表示成功获取到了数据
				return Promise.resolve();
			},

      // 里面的内容之前写过的,放进来,改一下
      // 别忘了getContentByHTTP(id)
			getContentByHTTP(id) {
				// 这一步是从服务器获取的,成功then
				return getCategoryContent(id).then(data => {
					return new Promise(resolve => {
						if (data) {
							this.content = data;
							// 成功之后才会执行resolve
							resolve();
						}
					});
				});
			},

			// 最后就是更新缓存了,会调用localStorage提供的api
			updateLocalStorage(contents, id, curTime) {
				// 先判断有没有值
				contents = contents || {};
				// 更新这个id对应的内容
				contents[id] = {};
				contents[id].data = this.content;
				// 目前更新的
				contents[id].updateTime = curTime;
				// 把内容设置进去,覆盖原来的
				storage.set(CATEGORY_CONTENT_KEY, contents);
			},

			// 返回顶部
			backToTop(speed) {
				this.$refs.scroll && this.$refs.scroll.scrollToTop(speed);
			},
			// 更新滚动条
			updateScroll() {
				this.$refs.scroll && this.$refs.scroll.update();
			}
		}
	};
</script>

<style lang='scss' scoped>
	@import '~assets/scss/mixins';

	.content-wrapper {
		position: relative;
		height: 100%;
	}

	.loading-container {
		position: absolute;
		top: 0;
		left: 0;
		z-index: $category-popup-z-index;
		@include flex-center();
		width: 100%;
		height: 100%;
		/*background-color: $modal-bgc;*/

		.mine-loading {
			color: #fff;
			font-size: 14px;
		}
	}
	.loading-wrapper {
		width: 50%;
		padding: 30px 0;
		background-color: $modal-bgc;
		border-radius: 4px;
	}

	.content {
		padding: 10px;
	}

	.pic {
		margin-bottom: 12px;

		&-link {
			display: block;
		}

		&-img {
			width: 100%;
		}
	}

	.section {
		margin-bottom: 12px;

		&:last-child {
			margin-bottom: 0;
		}

		&-title {
			height: 28px;
			line-height: 28px;
			color: #080808;
			font-weight: bold;
		}

		&-list {
			display: flex;
			flex-wrap: wrap;
			background-color: #fff;
			padding: 10px 10px 0;
		}

		&-item {
			width: (100% / 3);
		}

		&-link {
			display: block;
		}

		&-pic {
			position: relative;
			width: 80%;
			padding-bottom: 80%;
			margin: 0 auto;
		}

		&-img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		&-name {
			height: 36px;
			line-height: 36px;
			text-align: center;
			@include ellipsis();
		}
	}
	.g-backtop-container {
		bottom: 10px;
	}
</style>


