<template>
  <div class="category">
    <header class="g-header-container">
      <category-header/>
    </header>
    <div class="g-content-container">
      <div class="sidebar">
        <category-tab @switch-tab="getCurrentId"/>
      </div>
      <div class="main">
        <!-- tab和cont是兄弟组件 不可以直接通信 
         这里把tab里的id传入父组件index这,然后再传给子组件content -->
        <category-content :curId="curId"/>
      </div>
    </div>
  </div>
</template>

<script>
  import CategoryHeader from './header';
  import CategoryTab from './tab';
  import CategoryContent from './content';

  export default {
    name: 'Category',
    components: {
      CategoryHeader,
      CategoryTab,
      CategoryContent
    },
    data() {
      return {
        curId: ''
      }
    },
    methods: {
      getCurrentId(id) {
        this.curId = id;
      }
    },
  }
</script>

<style lang="scss" scoped>
  @import "~assets/scss/mixins";

  .category {
    overflow: hidden;
    width: 100%;
    height: 100%;
    background-color: $bgc-theme;
  }

  .g-header-container {
    display: flex;
  }

  // 左边tab滚动条滚动
  .g-content-container {
    display: flex;
  }

  .sidebar {
    width: 80px;
    height: 100%;
  }

  .main {
    flex: 1;
    height: 100%;
  }
</style>


